<script>
  import {
    Message
  } from 'element-ui'
  export default {
    data() {
      return {
        // 月份使用？
        pickerOptions2: {
          shortcuts: [{
            text: '最近一周',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近一个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近三个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit('pick', [start, end]);
            }
          }]
        },
        value4: [new Date(2000, 10, 10, 10, 10), new Date(2000, 10, 11, 10, 10)],
        value5: '',
        //***********************************________*******************************
        keywords: '',
        tableLoading: false,
        advanceSearchViewVisible: false,
        rules: {
          // note: [{required: true, message: '必填:备注', trigger: 'blur'}],
          // name: [{required: true, message: '必填:名称', trigger: 'blur'}],
          // quantity: [{required: true, message: '必填:数量', trigger: 'blur'}],
          // unitId: [{required: true, message: '必选:单位', trigger: 'blur'}],
        },
        types: 2,
        midProduct: [],
        plans: [],
        clients: [],
        units: [],
        dialogTitle: '',
        currentPage: 1,
        totalCount: -1,
        // 原材料
        totalMaterialCount: -1,
        keyMaterialwords: '',
        currentMaterialPage: 1,
        plan: {
          materialType: '',
          notes: '',
          serialNumber: '',
          clientName: ''
        },
        detail: {
          sid: 0,
          id: 0,
          clientId: 0,
          clientName: '',
          abbrName: '',
          quantity: '',
          notes: '',
          serialNumbers: [],
          name: '',
          proId: '', //产品Id
          specs: "", //规格
          stamp: '', //标记
          proNo: '', //型号

          type: '',
          specifications: ''
        },
        serialNumbers: [],
        serialNumber: {
          startNo: '',
          endNo: '',
          note: ''
        },
        details: [],
        dialogFormVisible: false,
        currentRowData: {}, //当前选中行数据
        currentIndex: '', //当前选中行号
        depTextColor: '',
        showOrHidePop: false,
        showHidePop: false,
        defaultProps: {
          label: 'name',
          isLeaf: 'leaf',
          children: 'child'
        },
        maxSid: 0,
        currentDeleteData: [],
        materials: [],
        materialList: [],
        products: [],
        dialogMaterialVisible: false,
        dialogMaterialTitle: '请选择产品',
        selectMaterial: {}, //当前选择的原料
        selectedM: {},
        filterText: '',
      }
    },
    mounted: function () {
      this.initData();
    },
    // watch: {
    //   filterText(val) {
    //     this.$refs.tree2.filter(val);
    //   }
    // },
    methods: {
      // 编号***********
      serialNumberAdd() {
        this.serialNumbers.push({
          startNo: '',
          endNo: ''
        });
      },
      deletSerialNo(index) {
        this.serialNumbers.splice(index, 1);
      },
      // 编号***********
      filterNode(value, data) {
        if (!value) return true;
        return data.label.indexOf(value) !== -1;
      },
      showRow(row) {
        this.selectedM = this.materialList.indexOf(row);
        this.selectMaterial = row;
        console.log(this.selectMaterial);
      },
      showMaterial() {
        this.loadMaterial();

      },
      chooseMaterial() {
        console.log('选择原料了！')
        // this.selectMaterial
        let vm = this;

        if (this.selectMaterial.id > 0) {
          const msg = this.selectMaterial.producteName + "" + this.selectMaterial.specification + ";"
          this.$confirm('您当前选择了' + msg + "确认选择该产品吗？", '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            vm.setDetailData(vm);
          }).catch(() => {
            this.$message({
              type: 'info',
              message: '已取消'
            });
          });
        } else {
          this.$message({
            type: 'info',
            message: '请选择原材料！'
          })
        }

      },
      setDetailData(that) {
        var v = that.selectMaterial;
        console.log(v);

        that.detail.name = v.producteName;
        that.detail.proId = v.id; //选取的物料Id
        that.detail.specs = v.specification;
        that.detail.proNo = v.proType

        this.dialogMaterialVisible = false;
      },
      cancelchoose: function () {
        console.log('选择框关闭')
        this.selectMaterial = {};
        this.selectedM = {};
      },
      formatStatus: function (row, column) {
        return row.materialType == 1 ? '已确认' : '未确认';
      },
      //materials: [["手套", 1, 38]]
      selectType(e, v, icon) {
        let that = this;
        console.log('changne'); //products  materials
        this.types = e;
        if (e == 2) {
          this.midProduct = [];
          this.midProduct = this.materials.concat(this.midProduct);
          this.emptyDetail()
        } else {
          this.midProduct = [];
          this.midProduct = this.products.concat(this.midProduct);
          this.emptyDetail()
        }
      },
      matterSelect(e) {
        console.log(e);
        let that = this;
        this.midProduct.forEach(function (v, i, arr) {
          if (v.id == e) {
            that.detail.unitId = v.unitId;
            that.detail.unitName = v.unitName;
            that.detail.name = v.name;
            that.detail.materialId = v.id; //选取的物料Id
            that.detail.specifications = v.specs;

          }
        })
      },
      addBox() {
        let that = this;
        console.log("asdfadsfasdfasd");
        if (this.detail.quantity == '' || this.detail.quantity == undefined) {
          Message('请输入数量');
          return false;
        }
        if (this.detail.name == '' || this.detail.name == undefined) {
          Message('请选择产品！');
          return false;
        }
        if (this.detail.sid == "") {
          if (this.details.length > 0) {
            //this.types 选择的类型原料/半成品
            //有之前相同的。。增加数量
            var flag = false;
            this.details.forEach(function (v, i, arr) {
              if (v.materialId == that.detail.materialId && v.mtype == that.types) {
                flag = true;
                v.quantity = parseFloat(that.detail.quantity) + parseFloat(v.quantity);
              }
            });
            if (!flag) {
              this.maxSid += 1;
              this.detail.sid = this.maxSid;
              this.detail.type = 'new'
              // this.detail.mtype = this.types;
              this.details.push(this.detail);
            }

          } else {
            this.detail.sid = 1;
            this.detail.type = 'new'
            this.detail.mtype = this.types;
            this.maxSid += 1;
            this.details.push(this.detail);
          };
        } else {
          if (this.details.length) {
            for (let index = 0, n = this.details.length; index < n; index++) {
              var e = this.details[index];
              if (e.type === 'odd') {
                this.detail.type = 'update'
                if (e.sid == this.detail.sid) {
                  this.details.splice(index, 1);
                  this.details.push(this.detail);
                }
              } else {
                if (e.sid == this.detail.sid) {
                  this.details.splice(index, 1);
                  this.details.push(this.detail);
                }
              }

            }
          }
        }

        this.emptyDetail();
      },
      editeDetail(index, row) {
        this.detail = row;
      },
      deleteDetail(index, row) {
        console.log('delete');
        var deletes = this.details.splice(index, 1);
        if (deletes[0].type === "odd") {
          deletes[0].type = 'delete'
          this.currentDeleteData.push(deletes[0]);
        }
        this.maxSid -= 1;
      },
      emptyDetail() {
        this.detail = {
          sid: 0,
          id: 0,
          name: '',
          quantity: '',
          unitId: '',
          notes: '',
          unitName: '',
          specifications: '',
          type: '',
        };
      },
      initData() {
        var _this = this;
        _this.loadData();
        this.getRequest("/marketingplan/basedata").then(resp => {
          if (resp && resp.status == 200) {
            var data = resp.data;
            // _this.units = data.root.units;
            // _this.clients = data.root.clients;
            var ma = data.root.clients;
            var mater = [];
            ma.forEach(function (v, i, arr) {
              var name = v[0],
                id = v[1],
                parentClientId = v[2];
              mater.push({
                name: name,
                unitId: id,
                parentClientId: parentClientId,
                parentName: v[3],
                address: v[4],
                phone: v[5],
                abbreviation: v[6],

              })
            });
            _this.clients = mater;
            _this.midProduct = [];
            _this.midProduct = _this.materials.concat(_this.midProduct);
            var pro = [];
            var p = data.root.products;
            p.forEach(function (v, i, arr) {
              pro.push({
                id: v[0],
                producteName: v[1],
                specification: v[2],
                proType: v[3],
                mark: v[4],
                color: v[5],

              })
            });
            _this.products = pro;
          }
        })

      },
      keywordsChange(val) {
        if (val == '') {
          this.loadData();
        }
      },
      keywordsMaterialChange(val) {
        if (val == '') {
          this.loadMaterial();
        }
      },
      handleNodeClick(data) {
        this.detail.clientId = data.id;
        this.detail.clientName = data.name;
        this.detail.abbrName = data.abbreviation;
        this.showOrHidePop = false;
        this.depTextColor = '#606266';
      },
      clearChoose() {
        this.detail.clientId = 0;
        this.detail.clientName = "";
      },
      showDepTree() {
        this.showHidePop = !this.showHidePop;
      },
      selectChange(e) {
        let that = this;
        console.log(e);
        if (that.units) {
          that.units.forEach(function (v, arr) {
            if (v.id == e) {
              console.log(v.id);
              that.detail.unitName = v.name;

            }
          })
        }

      },
      currentChange(currentChange) {
        this.currentPage = currentChange;
        console.log(this.currentPage);
        this.loadData();
      },
      currentMaterialChange(currentChange) {
        this.currentMaterialPage = currentChange;
        this.loadMaterial();
      },
      addAndFlushData() {
        this.dialogTitle = "郑铁公司销售单";
        let that = this;
        // this.getRequest("/marketingplan/getSerialNumber").then(resp => {
        //   that.tableLoading = false;
        //   if (resp && resp.status == 200) {
        //     var data = resp.data;
        //     that.plan.serialNumber = data.root.serialNumber;
        //   }
        // })
        that.serialNumbers = [];

        that.serialNumbers.push(this.serialNumber);

        that.dialogFormVisible = true;

      },
      searchData() {
        this.loadData();
      },
      searchMaterialData() {
        this.loadMaterial();
      },
      showEditEmpView(index, row) {
        console.log(row)
        var that = this;
        that.dialogTitle = "编辑信息";
        that.plan = row;
        that.plan.createDate = row.createDate;
        var currentPlan = _.cloneDeep(row);
        if (currentPlan.detail != undefined) {
          currentPlan.detail.forEach(function (v, i, arr) {
            v.type = 'odd';
            v.sid = i + 1;
          })
          this.details = currentPlan.detail;
          this.maxSid = currentPlan.detail.length;
        }
        that.dialogFormVisible = true;
      },
      add(addEmpForm) {
        this.dialogFormVisible = true;
        var _this = this;
        var plans = _.cloneDeep(this.plan);
        if (this.details.length == 0) {
          this.$message({
            type: 'info',
            message: '详情不能为空！'
          });
          return false;
        }
        var cache = [];
        var currentDetail = this.details.concat(this.currentDeleteData);
        var str = JSON.stringify(currentDetail, function (key, value) {
          if (typeof value === 'object' && value !== null) {
            if (cache.indexOf(value) !== -1) {
              // Circular reference found, discard key
              return;
            }
            // Store value in our collection
            cache.push(value);
          }
          return value;
        });
        cache = null;
        plans.detail = str;
        this.$refs[addEmpForm].validate((valid) => {
          if (valid) {
            // this.plan.buyedDate=new Date(this.plan.buyedDate);
            var flag = this.validateplanNo();
            if (this.plan.id) {
              //更新
              this.tableLoading = true;
              console.log(this.plan);

              this.postRequest("/marketingplan/update", plans).then(resp => {
                _this.tableLoading = false;
                if (resp && resp.status == 200) {
                  // var data = resp.data;
                  _this.dialogFormVisible = false;
                  _this.loadData();
                  _this.emptyData();
                  _this.emptyDetail();

                }
              })
            } else {
              //添加
              this.tableLoading = true;
              if (this.plan.clientId == 0 || this.plan.clientId == '') {
                this.plan.clientId = 0;
                plans.materialType = 1;
              } else {
                plans.materialType = 2;
              }
              console.log(plans);
              this.postRequest("/marketingplan/addnew", plans).then(resp => {
                _this.tableLoading = false;
                if (resp && resp.status == 200) {
                  var data = resp.data;
                  _this.dialogFormVisible = false;
                  _this.loadData();
                  _this.emptyData();
                  _this.emptyDetail();
                }
              })
            }
          } else {
            return false;
          }
        });
      },
      validateplanNo() {
        var flag = true;

        return flag;
      },
      cancelOpt() {
        this.dialogFormVisible = false;
        this.loadData();
        // var index=this.currentIndex;
        // this.plans[index] =this.currentRowData;
        console.log('o(*￣︶￣*)o')

      },
      deleteData(row) {
        this.$confirm('此操作将删除[' + row.serialNumber + '], 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.doDelete(row.id);
        }).catch(() => {});
      },
      doDelete(ids) {
        this.tableLoading = true;
        var _this = this;
        this.getRequest("/marketingplan/delet?id=" + ids).then(resp => {
          _this.tableLoading = false;
          if (resp && resp.status == 200) {
            var data = resp.data;
            _this.loadData();
          }
        })
      },
      loadData() {
        var _this = this;
        this.tableLoading = true;
        this.getRequest("/marketingplan/findbypage?page=" + this.currentPage + "&size=10&queryName=" + this.keywords)
          .then(
            resp => {
              this.tableLoading = false;
              if (resp && resp.status == 200) {
                var data = resp.data.data;
                _this.plans = data;
                _this.totalCount = resp.data.total;
                _this.emptyData();
              }
            })
      },
      loadMaterial() {
        var _this = this;
        this.tableLoading = true;
        this.getRequest("/product/findbypage?page=" + this.currentMaterialPage + "&size=10&queryName=" + this
          .keyMaterialwords).then(resp => {
          this.tableLoading = false;
          if (resp && resp.status == 200) {
            var data = resp.data.data;
            _this.materialList = data;
            _this.totalMaterialCount = resp.data.total;
            _this.dialogMaterialVisible = true;
            // _this.emptyData();
            // 清除选择的行
          }
        })
      },
      handleCommand(cmd) {
        var _this = this;
        if (cmd == 'logout') {
          this.$confirm('注销登录, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            // _this.getRequest("/logout");
            // _this.$store.commit('logout');
            _this.$router.replace({
              path: '/'
            });
          }).catch(() => {
            _this.$message({
              type: 'info',
              message: '取消'
            });
          });
        }
      },
      emptyData() {
        this.details = [];
        this.plan = {
          materialType: '',
          notes: '',
          serialNumber: '',
          clientId: 0,
          clientName: ''
        }

      }
    },
    computed: {
      routes() {
        return this.$store.state.routes
      }
    }
  }

</script>
<style>
  .el-submenu {
    width: 180px;
    min-width: 175px;
  }

  .demo-table-expand {
    font-size: 0;
  }

  .demo-table-expand label {
    width: 50px;
    color: #2974df;
  }

  .demo-table-expand .el-form-item {
    margin-left: 10px;
    margin-bottom: 0;
    width: auto;
  }

</style>



